import * as d3 from "d3";
import * as topojson from "topojson";

function initializeMap() {
  const windowWidth = window.innerWidth;
  const windowHeight = window.innerHeight;

  const svg = d3.select('body')
    .append('svg')
    .attr('width', windowWidth)
    .attr('height', windowHeight);

  const projection = d3.geoEquirectangular()
    .fitWidth(windowWidth, { type: "Sphere" });

  const path = d3.geoPath(projection);
  const g = svg.append('g');

  return { svg, g, path };
}

function fetchApiData() {
  return fetch('https://restcountries.com/v3.1/all')
    .then(response => response.json())
    .catch(error => {
      console.error('Error fetching data:', error);
      return [];
    });
}

function updateMapWithData(svg, g, path, apiData) {
  g.selectAll('path')
    .data(apiData)
    .enter()
    .append('path')
    .attr('class', 'country')
    .attr('data-country', d => d.name) // Modify this to match the structure of the API data
    .attr('d', path);
}

export default function main() {
  const { svg, g, path } = initializeMap();

  fetchApiData()
    .then(apiData => {
      updateMapWithData(svg, g, path, apiData);
    });
}

